import { Link } from 'react-router-dom';
import React, { useState } from 'react';
import Header from './Header.js';

function Register(props) {

  const [userData, setUserData] = useState({
    email: '',
    password: '',
    message: ''
  });

  function handleChange(e) {
    const { name, value } = e.target;
    setUserData({
      ...userData,
      [name]: value
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    // TODO add if (!userData.email || !userData.password)
    const { email, password } = userData;

    props.onRegister({ email, password })
  }

  return (
      <>
      <Header onSignOut={props.onSignOut} email={props.email} loggedIn={false} nameLink="–í–æ–π—Ç–∏" way="/signin" />
      <form className="start-form" onSubmit={handleSubmit}>
        <h1 className="start-form__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h1>
          <input id="email-input" name="email" className="start-form__input" placeholder="Email" 
          value={userData.email} onChange={handleChange} />
          <input id="password-input" type="password" name="password" className="start-form__input" placeholder="–ü–∞—Ä–æ–ª—å"
          value={userData.password} onChange={handleChange} />
          <button type="submit" className="start-form__button">–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
      </form>

      <div className="start__signin">
        <p>–£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã? <Link to="/signin" className="start__login-link">–í–æ–π—Ç–∏</Link></p>
      </div>
      </>
  );
  }
  
  export default Register;