import React, { useState } from 'react';
import Header from './Header.js';

function Login(props) {

  const [userData, setUserData] = useState({
    email: '',
    password: '',
    message: ''
  });

  function handleChange(e) {
    const { name, value } = e.target;
    setUserData({
      ...userData,
      [name]: value
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    if (!userData.email || !userData.password){
      return;
    }
    const { email, password } = userData;

    props.onLogin({ email, password })
  }

    return (
        <>
        <Header onSignOut={props.onSignOut} email={props.email} loggedIn={false} nameLink="–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è" way="/signup" />
        <form className="start-form" onSubmit={handleSubmit}>
          <h1 className="start-form__title">–í—Ö–æ–¥</h1>
            <input id="email-input" name="email" className="start-form__input" placeholder="Email" 
            value={userData.email} onChange={handleChange} />
            <input id="password-input" type="password" name="password" className="start-form__input" placeholder="–ü–∞—Ä–æ–ª—å"
            value={userData.password} onChange={handleChange} />
            <button type="submit" className="start-form__button">–í–æ–π—Ç–∏</button>
        </form>
        </>
    );
  }
  
  export default Login;